// css components
.button {
    display: flex;
    align-items: center;
    @include text(1.2rem,is,1.8rem,$gold-cl,uppercase);
    letter-spacing: .1em;
    padding: 10px 18px 10px 20px;
    border: solid 1px $gold70-cl;
    cursor: pointer;
    position: relative;
    img {
        float: left;
        margin-right: 8px;
    }
    &.--big {
        padding: 18px 14px 18px 20px;
    }
}

.--hyperion::before,
.--hyperion::after {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.--hyperion {
	border: solid 1px $gold70-cl;
	overflow: hidden;
    background-color: $gold-cl;
}

.--hyperion span {
	display: flex;
	position: relative;
}

.--hyperion > span {
	overflow: hidden;
}

.--hyperion > span > span {
	overflow: hidden;
	mix-blend-mode: difference;
}

.--hyperion:hover > span > span {
	animation: MoveUpInitial 0.2s forwards, MoveUpEnd 0.2s forwards 0.2s;
}
.--hyperion:hover > span > span:nth-child(2) {
	animation: MoveUpInitial 0.3s forwards, MoveUpEnd 0.3s forwards 0.2s;
}


@keyframes MoveUpInitial {
	to {
		transform: translate3d(0,-105%,0);
	}
}

@keyframes MoveUpEnd {
	from {
		transform: translate3d(0,100%,0);
	}
	to {
		transform: translate3d(0,0,0);
	}
}

.--hyperion::before {
	content: '';
	//background-color: $gold-cl;
    background-color: $background-cl;
	transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
	transform-origin: 100% 50%;
}

.--hyperion:hover::before {
	transform: scale3d(0,1,1);
	transform-origin: 0% 50%;
}

