.popup-info {
    max-width: 586px;
    background-color: #0a0a0a;
    padding: 20px;
    position: fixed;
    left: 21px;
    bottom: 23px;
    z-index: 800;
    pointer-events: none;
    opacity: 0;
    @include transition($t);
    @include transform(translateY(-25px));
    .overlay-popup {
        display: none;
    }
    .popup-wrapper {
        @include size(100%);
        padding: 32px 29px 32px 29px;
        border: solid 1px $gold40-cl;
        position: relative;
        .ic-close {
            @include size(40px);
            @include mid(true);
            border: solid 1px $gold40-cl;
            position: absolute;
            top: 10px;
            right: 10px;
            cursor: pointer;
            @include transition($t);
                &:hover {
                    border: solid 1px $gold-cl;
                    @include transition($t);
                }
        }
        .--h4 {
            text-transform: uppercase;
            margin-bottom: 23px;
            color: $white90-cl;
        }
        .popup-info__content {
            width: 100%;
            height: auto;
            max-height: 249px;
            overflow: hidden;
            .--text {
                height: 100%;
                color: $white60-cl;
                overflow: scroll;
            }
        }

    }
    &.active {
        pointer-events: auto;
        opacity: 1;
        @include transition($t);
        @include transform(translateY(0px));
    }
    @include sm {
        max-width: calc(100% - 40px);
        background-color: #0a0a0a;
        padding: 20px;
        position: fixed;
        left: 20px;
        right: 20px;
        bottom: 42px;
        top: 42px;
        .overlay-popup {
            display: block;
            opacity: 1;
            pointer-events: none;
            position: absolute;
            top: -42px;
            left: -20px;
            right: -20px;
            bottom: -42px;
            border-top: solid 42px $black90-cl;
            border-bottom: solid 42px $black90-cl;
            border-left: solid 20px $black90-cl;
            border-right: solid 20px $black90-cl;
        }
        .popup-wrapper {
            @include size(100%);
            padding: 32px 26px 49px 26px;
            border: solid 1px $gold40-cl;
            position: relative;
            .--h4 {
                width: 90%;
            }
            .popup-info__content {
                height: calc(100% - 100px);
                max-height: none;
                .--text {
                    overflow: scroll;
                }
            }
        }
    }
}
.popup-signup {
    @include size(100vw, 100vh);
    background-color: $black90-cl;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 900;
    pointer-events: none;
    opacity: 0;
    @include transition($t);
    &__wrapper {
        position: absolute;
        top: 50%;
        left: 50%;
        @include transform(translateX(-50%) translateY(-50%));
        width: 100%;
        max-width: 626px;
        height: auto;
        padding: 20px; 
        background-color: #0a0a0a;       
        .poup-signup__content {
            border: solid 1px $gold40-cl;
            width: 100%;
            position: relative;
            padding: 40px 32px 60px;
            .ic-close {
                @include size(40px);
                @include mid(true);
                border: solid 1px $gold40-cl;
                position: absolute;
                top: 10px;
                right: 10px;
                cursor: pointer;
                @include transition($t);
                &:hover {
                    border: solid 1px $gold-cl;
                    @include transition($t);
                }
            }
            .--h4 {
                text-transform: uppercase;
                margin-bottom: 23px;
                color: $white90-cl;
                width: 90%;
                margin-bottom: 27px;
            }
            .form__input {
                position: relative;
                label {
                    position: absolute;
                    top: 15px;
                    left: 12px;
                    display: flex;
                    justify-content: center;
                    color: $gold60-cl;
                    img {
                        margin-right: 12px;
                    }
                }
                input,
                textarea {
                    width: 100%;
                    height: 52px;
                    border: solid 1px $white10-cl;
                    background-color: transparent;
                    margin-bottom: 20px;
                    outline: 0;
                }
                textarea {
                    height: 110px;
                    margin-bottom: 30px;
                }
            }
            .form__submit {
                display: flex;
                justify-content: space-between;
                .form-hotline {
                    display: flex;
                    align-items: center;
                    .hotline-text {
                        @include text(1.2rem,is,1.8rem,$white30-cl,uppercase);
                        letter-spacing: .12rem;
                        margin-left: 10px;
                        span {
                            font-family: ie;
                            color: $gold-cl;
                        }
                    }
                }
                button {
                    outline: 0;
                    border: none;
                    padding: 19px 58px;
                    background-color: $gold-cl;
                    @include text(1.2rem,is,1.8rem,$white90-cl,uppercase);
                    letter-spacing: .12rem;
                    position: relative;
                    cursor: pointer;
                }
                button::before,
                button::after {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
                button {
                    border: 1px solid $gold-cl;
                    overflow: hidden;
                    color: $white-cl;
                    background-color: $background-cl;
                }
                
                button span {
                    display: block;
                    position: relative;
                }
                
                button > span {
                    overflow: hidden;
                }
                
                button > span > span {
                    overflow: hidden;
                    //mix-blend-mode: difference;
                }
                
                button:hover > span > span {
                    color: $gold-cl;
                    animation: MoveUpInitial 0.2s forwards, MoveUpEnd 0.2s forwards 0.2s;
                }
                
                @keyframes MoveUpInitial {
                    to {
                        transform: translate3d(0,-105%,0);
                    }
                }
                
                @keyframes MoveUpEnd {
                    from {
                        transform: translate3d(0,100%,0);
                    }
                    to {
                        transform: translate3d(0,0,0);
                    }
                }
                
                button::before {
                    content: '';
                    background: $gold-cl;
                    transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
                    transform-origin: 100% 50%;
                }
                
                button:hover::before {
                    transform: scale3d(0,1,1);
                    transform-origin: 0% 50%;
                }
            }
        }
    }
    &.active {
        opacity: 1;
        pointer-events: auto;
        @include transition($t);
    }
    @include xs {
        &__wrapper {
            @include size(calc(100vw - 40px), auto);
            top: 50%;
            left: 20px;
            right: 20px;
            @include transform(translateY(-50%));
            .poup-signup__content {
                padding: 32px 26px 49px;
                .form__input {
                    textarea {
                        margin-bottom: 20px;
                    }
                }
                .form__submit {
                    flex-direction: column-reverse;
                    align-items: center;
                    button {
                        width: 100%;
                        margin-bottom: 22px;
                    }
                }
            }
        }
    }
    &.--success {
        opacity: 0;
        pointer-events: none;
        @include transition($t);
        .popup-signup__wrapper {
            .poup-signup__content {
                text-align: center;
                .--h4 {
                    margin-top: 82px;
                    width: 100%;
                    margin-bottom: 16px;
                }
                .popup-decor {
                    margin: 0 auto 20px;
                }
                .--text.--success {
                    padding: 0 52px;
                    margin: 0 auto 50px;
                    color: $white60-cl;
                }
                button {
                    outline: 0;
                    border: none;
                    padding: 19px 58px;
                    background-color: $gold-cl;
                    @include text(1.2rem,is,1.8rem,$white90-cl,uppercase);
                    letter-spacing: .12rem;
                    position: relative;
                    cursor: pointer;
                }
                button::before,
                button::after {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
                button {
                    border: 1px solid $gold-cl;
                    overflow: hidden;
                    color: $white-cl;
                    background-color: $background-cl;
                }
                
                button span {
                    display: block;
                    position: relative;
                }
                
                button > span {
                    overflow: hidden;
                }
                
                button > span > span {
                    overflow: hidden;
                    //mix-blend-mode: difference;
                }
                
                button:hover > span > span {
                    color: $gold-cl;
                    animation: MoveUpInitial 0.2s forwards, MoveUpEnd 0.2s forwards 0.2s;
                }
                
                @keyframes MoveUpInitial {
                    to {
                        transform: translate3d(0,-105%,0);
                    }
                }
                
                @keyframes MoveUpEnd {
                    from {
                        transform: translate3d(0,100%,0);
                    }
                    to {
                        transform: translate3d(0,0,0);
                    }
                }
                
                button::before {
                    content: '';
                    background: $gold-cl;
                    transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
                    transform-origin: 100% 50%;
                }
                
                button:hover::before {
                    transform: scale3d(0,1,1);
                    transform-origin: 0% 50%;
                }
            }
        }
        &.active {
            opacity: 1;
            pointer-events: auto;
