// css header
header {
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 800;
    background-color: rgba(0,0,0,0);
    backdrop-filter: blur(0px);
    -webkit-backdrop-filter: blur(0px);
    @include transition($t);
    .container {
        padding-top: 48px;
        padding-bottom: 48px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include transition($t);
        .logo {
            display: flex;
            align-items: center;
            .sound-icon {
                margin-left: 26px;
                display: flex;
                flex-direction: column;
                align-items: center;                
            }
            .sound-wave {
                width: 26px;
                height: 15px;
                display: flex;
                align-items: center;
                cursor: pointer;
                .bar {
                    display: block;
                    width: 1px;
                    border-radius: 15px;
                    margin-right: 3px;
                    height: 13px;
                    background: $white60-cl;
                    animation: sound 0ms -780ms linear infinite alternate;
                    transition: height 1s;
                    @include transition($t);
                }
            }
            @keyframes sound {
                0% {
                   opacity: .6;
                    height: 2px; 
                }
                100% {
                    opacity: 1;       
                    height: 13px;
                }
            }
            .bar:nth-child(1) {
                height: 2px; 
                animation-duration: 580ms;
            }
            .bar:nth-child(2) {
                height: 4px; 
                animation-duration: 600ms;
            }
            .bar:nth-child(3) {
                height: 6px; 
                animation-duration: 620ms;
            }
            .bar:nth-child(4) {
                height: 8px; 
                animation-duration: 640ms;
            }
            .bar:nth-child(5) {
                height: 10px; 
                animation-duration: 660ms;
            }
            .bar:nth-child(6) {
                height: 12px; 
                animation-duration: 680ms;
            }
            .sound-icon:hover {
                .sound-wave {
                    .bar {
                        background: $white-cl;
                        @include transition($t);
                    }
                }
            }
            .sound-icon.disabled {
                .bar {
                    height: 2px;
                    animation-iteration-count: 1;
                    animation-fill-mode: backwards;
                    transition: height 1s;
                }
            }
        }
        .menu {
            display: flex;
            align-items: center;
            li {
                .link {
                    margin: 0 28px;
                    letter-spacing: .1em;
                    font-family: is;
                    opacity: .3;
                    @include text(1.2rem,is,1.8rem,$white-cl,uppercase);
                    @include transition($t);
                    cursor: pointer;
                    &:hover,
                    &.active {
                        opacity: 1;
                        @include transition($t);
                    }
                }
            }
            span {
                width: 1px;
                height: 12px;
                background-color: $white20-cl;
            }
        }
        .extra {
            display: flex;
            align-items: center;
            &_lang {
                margin-right: 22px;
                a {
                    @include text(1.2rem,is,1.8rem,$white30-cl,uppercase);
                    @include transition($t);
                    &:hover  {
                        color: $white-cl;
                        @include transition($t);
                    }
                }
            }
        }
    }
    &.scroll {
        background-color: rgba(0,0,0,.3);
        backdrop-filter: blur(20px);
        -webkit-backdrop-filter: blur(20px);
        @include transition($t);
        .container {
            padding-top: 15px;
            padding-bottom: 15px;
            @include transition($t);
        }
    }
    @include md {
        width: 100%;
        position: fixed;
        top: 0;
        z-index: 800;
        .container {
            .menu {
                li {
                    a {
                        margin: 0 16px;
                    }
                }
            }
        }
    }
    @include sm {
        display: none;
    }
}